    // import {uniLoadMore} from "@dcloudio/uni-ui"
	export default {
        onLoad() {
           this.list = []
           this.getList()
        },
        onPullDownRefresh(){
            console.log("下拉")
        },
        onReachBottom(){
            this.page = this.page + 1
            this.getList()
        },
        // components: {uniLoadMore},
		data() {
			return {
				list:[],
                soureidList:[],
                page:1,
			}
		},
		methods: {
			getList(){
                uni.showLoading({
                    title: '正在加载中...',
                    mask: false
                });
                uni.request({
                    url: 'https://www.apiopen.top/satinGodApi', //仅为示例，并非真实接口地址。
                    data: {
                        type: '2',
                        page: this.page
                    },
                    header: {
                        // 'custom-header': 'hello' //自定义请求头信息
                    },
                    success: (res) => {
                        // console.log(res.data);
                        uni.hideLoading();
                        if(res.data.code===200){
                            this.list.push(...res.data.data);
                        }
                    }
                });
            },
            openDetail(item){
                uni.navigateTo({
                    // ? 后面加要传的参数， 多个参数用 & 隔开 
                    url: '../joke-detail/joke-detail?id='+item.soureid
                })
            },
            collect(item){
                // console.log(item.soureid)
                if(this.soureidList.indexOf(item.soureid)<0){
                    this.soureidList.push(item.soureid)
                    uni.showToast({
                        title:'收藏成功！'
                    })
                }else{
                    this.soureidList.splice(this.soureidList.indexOf(item.soureid),1)
                    uni.showToast({
                        title:'取消收藏！'
                    })
                }
            },
            share(item){
                uni.showModal({
                    content: '分享功能正在开发中...',
                    showCancel: false
                });
            }
		}
	}
